import Vue from 'vue';
import App from './App';

import Login from './components/Login';
import Register from './components/Register';
import Shop from './components/Shop.vue';
import Home from './components/Home.vue';

import User from './components/account/User.vue';
import History from './components/account/History.vue';
import Cart from './components/account/Cart.vue';

import Keen from 'keen-ui';

import VueRouter from 'vue-router';
import VueResource from 'vue-resource';

import authService from "./auth.js";


Vue.use(VueRouter);
Vue.use(VueResource);
Vue.use(Keen);

const router=new VueRouter();
const public_urls=[
    '/home',
    '/register',
    '/login'
];
router.map({
    '/login':{
        component:Login
    },
    '/register':{
        component:Register
    },
    '/home':{
        component:Home
    },
    '/shop':{
        component:Shop
    },
    '/user/info':{
        component:User
    },
    '/user/history':{
        component:History
    },
    'user/cart':{
        component:Cart
    }
});

router.redirect({
    '*':'/home'
});

router.beforeEach(function(transition){
    if(public_urls.indexOf(transition.to.path) < 0
       &&!authService.isLoggedIn()){
        transition.redirect('/login');
    }else{
        transition.next();
    }
});

router.start(App,'#app');



