<template>
    <div class="wrapper" >
        <nav class="navbar navbar-default" v-show="show">
            <div class="container">
                <ul class="nav navbar-nav">
                    <li>
                        <ui-button type="flat"
                            text="主页"
                            class="top-bar" type="flat"
                            :color="type=='home'?'primary':'defualt'"
                            @click="this.$router.go('/home')"
                            icon="home">
                        </ui-button>
                    </li>
                    <li>
                        <ui-button
                            :text="user"
                            class="top-bar" type="flat"
                            :color="type=='user'?'primary':'default'"
                            @click="this.$router.go('/user/info')"
                            icon="account_circle">
                        </ui-button>
                    </li>
                    <li>
                        <ui-button
                            text="注销" v-show="loggedin"
                            class="top-bar" type="flat"
                            @click="auth.logout()"
                            icon="input">
                        </ui-button>
                    </li>
                </ul>
                <ul class="navbar-nav nav" v-show="btnShow">
                    <li v-for="button in buttons">
                        <ui-button
                            :text="button.tip" class="top-bar" type="flat"
                            @click="this.$router.go(button.click)"
                            :icon="button.icon">
                        </ui-button>
                    </li>
                </ul>
                <ui-textbox name="search" placeholder="Search" class="pull-right top-bar"
                            icon="search" hide-label
                            :value.sync="searchText"
                            @keydown-enter="search">
                </ui-textbox>
            </div>
        </nav>
        <router-view></router-view>
    </div>
</template>

<script>
    import auth from './auth.js';
    export default {
        name: 'app',
        methods:{
            reset:function(){
                this.btnShow=false;
                this.show=false;
            },
            search:function(){}
        },
        data () {
            var that=this;
            auth.change=function(){
                if(auth.active){
                    that.$set('loggedin',true);
                    that.$set('user',auth.user.name);
                }else{
                    that.$set('loggedin',false);
                    that.$set('user','登录');
                }
            }
            return {
                user:'登录',
                type:'home',
                show:true,
                searchText:'',
                loggedin:false,
                btnShow:false,
                buttons:{
                    info:{icon:'person',tip:'账户信息',path:'/user/info'},
                    history:{icon:'history',tip:'订单历史',path:'/user/history'},
                    cart:{icon:'shopping_cart',tip:'购物车',path:'/user/cart'}
                }
            }
        }
    }
</script>

<style>
@import "~keen-ui/dist/min/keen-ui.min.css";
body{
    background-color:#cccccc;
}
.wrapper{
    position:fixed;
    top:0;
    width:100%;
}
.navbar{
    background-color:rgba(33,150,243,.12);
    border:0;
    margin-bottom:0;
}
.top-bar{
    margin:4px 0;
}
.top-divider{
    margin-top:12px;
}
</style>



