function authService(){
    this.user={};
    this.active=false;

    this.isLoggedIn= function(){
        return this.active;
    };

    this.reset=function(){
        this.user={};
        this.active=false;
    };

    this.change=null;

    var that=this;
    this.check=function(http){
        this.login(http,'','');
        return this.active;
    };
    this.login= function(http,name,passwd,success,fail){
        http.post('/passwd',{name:name,passwd:passwd})
            .then(function(res){
                if(res.data.error){
                    that.user={};
                    that.active=false;
                    that.change&&that.change();
                    fail&&fail(res.data.error,res);
                }else{
                    that.user=res.data;
                    that.user.name=name;
                    that.user.passwd=passwd;
                    that.active=true;
                    that.change&&that.change();
                    success&& success(that.user,res);
                }
            },function(res){
                fail&&fail("请重试",res);
            });
    };
    this.logout=function(){
        console.log('logout');
    };
    return this;
}

module.exports=new authService();


