<template>
    <div class="container">
        <div class="col-md-2"> </div>
        <div class="login col-md-8">
            <ui-alert type="warning" text="Wrong username or password"
                      :show.sync="showMsgp" :text="msg">
            </ui-alert>
            <div class="row">
                <div class="col-md-6">
                    <ui-textbox
                        label="Username" name="username"
                        type="text" placeholder="Enter a username"
                        icon="person"  validation-rules="required|min:6" icon-right
                        :value.sync="name">
                    </ui-textbox>
                    <ui-textbox name="passwd" type="password"
                                placeholder="Enter your passward"
                                label="Password" icon="fingerprint" icon-right
                                validation-rules="required|min:8" :value.sync="passwd">
                    </ui-textbox>
                    <ui-textbox name="passwd-again" type="password"
                                placeholder="Enter your passward again"
                                icon="fingerprint" icon-right
                                validation-rules="required" :value.sync="passwdAgain">
                    </ui-textbox>
                </div> 
                <div class="col-md-6">
                    <ui-textbox name="telephone" type="tel" label="Telephone"
                                placeholder="Enter your telephone number"
                                icon="phone" icon-right
                                validation-rules="required" :value.sync="tel">
                    </ui-textbox>
                    <ui-textbox name="address" type="text" label="Address"
                                validation-rules="required" :value.sync="addr">
                    </ui-textbox>
                    <ui-radio-group
                        :options="options"
                        label="Gender" :value.sync="gender"
                        name="gender">
                    </ui-radio-group>
                </div>
            </div>
            <hr />
            <div class="row">
                <div class="col-md-12">
                    <ui-button text="I have an  account" button-type="button"
                               @click="redirect">
                    </ui-button>
                    <ui-button text="create" class="pull-right"  button-type="submit"
                               color="primary"
                               @click="submit">
                    </ui-button>
                </div>
            </div>
        </div>
        <div class="col-md-2"> </div></div>
  </template>

<script>
    export default {
        ready(){
            this.$parent.reset();
        },
        data (){
            return {
                name:'',
                passwd:'',
                passwdAgain:'',
                tel:'',
                gender:'',
                addr:'',
                options:['male','female'],
                showMsgp:false,
                msg:''
            };
        },
        methods:{
            redirect:function(){
                this.$router.go('/login');
            },
            submit:function(event){
                if(this.passwd!=this.passwdAgain){
                    this.msg="Password not match";
                    this.showMsgp=true;
                    console.log(this.passwordAgain,this.passwd);
                    return;
                }
                // post data
            }
        }
    }
   </script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.login{
       margin-top:20%;
       margin-left:10px;
       margin-right:10px;
   }
   hr{
       border:1px solid;
   }
   </style>



