<template>
  <div class="container">
    <div class="col-md-4"> </div>
    <div class="login col-md-4">
        <ui-alert type="warning"
                  :text.sync="message"
                  :show.sync="showMsgp">
        </ui-alert>
        <ui-textbox
            label="用户名" name="username" type="text"
            placeholder="输入用户名"
            :validation-messages="nameMsg"
            icon="person"  validation-rules="required|min:6" icon-right
            :valid.sync="nameValid"
            :value.sync="name">
        </ui-textbox>
        <ui-textbox name="passwd" type="password" placeholder="输入密码"
                    label="密码" icon="fingerprint" icon-right
                    :validation-messages="passwdMsg"
                    validation-rules="required"
                    :valid.sync="passwdValid"
                    :value.sync="passwd">
        </ui-textbox>
        <ui-button text="注册" button-type="button"
                   @click="this.$router.go('/register')">
        </ui-button>
        <ui-button text="游客" button-type="button"
                   @click="this.$router.go('/home')">
        </ui-button>
        <ui-button text="提交" class="pull-right"  button-type="submit"
                   color="primary"
                   @click="submit">
        </ui-button>
    </div>
    <div class="col-md-4"> </div></div>
  </template>

<script>
    import authService from '../auth.js';
    export default {
        ready(){
            this.$parent.reset();
        },
        data (){
            return {
                name:'',
                passwd:'',
                showMsgp:false,
                nameValid:false,
                passwdValid:false,
                message:'',
                nameMsg:{
                    required:"用户名必须填写",
                    min:'长度至少为6'
                },
                passwdMsg:{
                    required:"密码必须填写"
                }
            };
        },
        methods:{
            submit:function(event){
                var that=this;
                if(this.nameValid&&this.passwdValid){                    
                    authService.login(
                        this.$http,
                        this.name,
                        this.passwd,
                        function(user,res){
                            var next='/home';
                            if(user.level===1)next='/seller';
                            else if(user.level===2)next='/admin';
                            that.$router.go(next);
                        },
                        function(msg,res){
                            that.$set('message',msg);
                            that.$set('showMsgp',true);
                        });
                }else{
                    this.message="表单必须有效"
                    this.showMsgp=true;
                }
                this.$broadcast('ui-input::reset');
                event.preventDefault();
            }
        }
    }
   </script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.login{
       margin-top:20%;
       margin-left:10px;
       margin-right:10px;
   }
   </style>



